p {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

address {
  font-style: normal;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
}

article {
  background: none;
  border: 0;
  outline: 0;
  margin: 0;
  padding: 0;
  font-size: 100%;
  display: block;
}

:root {
  --title-font-family: "Poppins", sans-serif;
  --primary-font-family: "Poppins", sans-serif;
  --gallery-font-family: "Roboto", sans-serif;
  --light-bg-color: #fcfcfc;
  --dark-bg-color: #202025;
  --white-color: #fcfcfc;
  --black-color: #000;
  --medium-grey-color: #5f6775;
  --title-light-color: #202025;
  --title-dark-color: #fcfcfc;
  --filter-letter-light-color: #5f6775;
  --filter-letter-dark-color: #5f6775;
  --modal-text-light-color: #5f6775;
  --modal-text-dark-color: #fcfcfc;
  --modal-title--black: #000917;
  --btn-bg-color: #fe7031;
  --btn-inmodal-bg-color: #fd5103;
  --btn-text-color: #fd5103;
  --btn-outline-color: #ff8650;
  --pretitle-hero-color: #fd5103;
  --accent-color: #fd5103;
  --btn-hover-color: #fd5103;
  --card-item: 1;
  --card-gap: 20px;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
  --body-background-color: #fff;
  --accent-theme-color: #fd5103;
  --theme-dark-color: #5f6775;
  --manu-theme-color: #202025;
  --search-input-color: #fff;
  --btn-mob-menu-color: #202025;
  --hero-text-color: #202025;
  --hero-bg-input-color: #fcfcfc;
  --gallery-btn-fav-color: #fff;
  --gallery-title-color: #000;
  --gallery-subtitle-color: #5f6775;
  --modal-bg-color: #fff;
  --modal-title-color: #000917;
  --modal-text-color: #5f6775;
  --modal-accent-text-color: #000;
  --background-color: #00000080;
  --modal-close-icon-color: #202025;
}

.dark-theme {
  --body-background-color: #202025;
  --title-light-color: #fcfcfc;
  --accent-theme-color: #fcfcfc;
  --theme-dark-color: #fd5103;
  --manu-theme-color: #fcfcfc;
  --search-input-color: #202025;
  --btn-mob-menu-color: #fcfcfc;
  --hero-text-color: #fcfcfc;
  --hero-bg-input-color: #202025;
  --gallery-btn-fav-color: #202025;
  --gallery-title-color: #fcfcfc;
  --gallery-subtitle-color: #fcfcfc;
  --modal-bg-color: #202025;
  --modal-title-color: #fcfcfc;
  --modal-text-color: #bdbdbd;
  --modal-accent-text-color: #fcfcfc;
  --background-color: #fff3;
  --modal-close-icon-color: #fcfcfc;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--body-background-color);
  margin: 0;
  font-style: normal;
  font-weight: 500;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 22px;
  font-weight: 600;
  line-height: 1.91;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.card-set {
  gap: var(--card-gap);
  flex-wrap: wrap;
  display: flex;
}

.card-set-item {
  flex-basis: calc((100% - (var(--card-item)  - 1) * var(--card-gap)) / var(--card-item));
}

.container {
  width: 100%;
  margin: 0 auto;
}

@media (min-width: 320px) {
  .container {
    width: 320px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.section {
  padding-top: 60px;
  padding-bottom: 40px;
}

@media (min-width: 768px) {
  .section {
    padding-top: 68px;
    padding-bottom: 60px;
  }
}

@media (min-width: 1280px) {
  .section {
    padding-top: 37px;
    padding-bottom: 60px;
  }
}

.section__title {
  text-align: center;
  color: var(--title-color);
  font-family: var(--title-font-family);
}

@media (min-width: 768px) {
  .section__title {
    font-weight: 700px;
    font-size: 32px;
    line-height: 1.31;
  }
}

@media (min-width: 1280px) {
  .section__title {
    font-size: 48px;
    line-height: 1.5;
  }
}

.btn {
  text-align: center;
  font-family: inherit;
  font-style: inherit;
  border-width: 0;
  border-color: #0000;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  display: inline-flex;
}

.backdrop__mobile {
  z-index: 5;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
}

@media (min-width: 320px) {
  .backdrop__mobile {
    width: 100vw;
    height: 100vh;
    background-color: var(--light-bg-color);
    flex-direction: column;
    align-items: flex-start;
    padding: 108px 20px 20px;
    display: none;
    position: fixed;
  }
}

@media (min-width: 480px) {
  .backdrop__mobile {
    padding: 20px 15px;
  }
}

@media (min-width: 768px) {
  .backdrop__mobile {
    margin-left: auto;
  }
}

.backdrop__mobile.is-open {
  display: flex;
}

.backdrop__mobile.is-hidden {
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
}

.menu__container {
  width: 100%;
  background-color: var(--light-bg-color);
  padding: 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  transform: translate(-50%, -50%)scale(1);
}

.btn__burger-menu-close:hover, .btn__burger-menu-close:focus {
  fill: var(--btn-hover-color);
}

.menu__list {
  font-size: 28px;
  font-weight: 400;
  line-height: .86;
}

.menu__list:not(:last-child) {
  margin-bottom: 32px;
}

.backdrop {
  width: 100%;
  height: 100%;
  background: var(--background-color);
  position: fixed;
  top: 0;
  left: 0;
}

.modal__ingredient, .modal__cocktail {
  width: 822px;
  height: 598px;
  background: var(--modal-bg-color);
  overflow: overlay;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

.modal__ingredient {
  width: 715px;
  height: 537px;
}

.modal__wrap {
  align-items: flex-start;
  display: flex;
}

.cocktail__img {
  width: 288px;
  height: 320px;
}

.cocktail__modal-btn {
  justify-content: flex-end;
  margin-bottom: 28px;
  margin-right: 28px;
  display: flex;
}

.modal__btn {
  text-align: center;
  width: 185px;
  height: 48px;
  font-family: var(--gallery-font-family);
  letter-spacing: .02em;
  color: var(--white-color);
  background: var(--btn-text-color);
  border-color: #0000;
  border-radius: 4px;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.2;
}

.modal__btn:hover {
  background: var(--btn-bg-color);
  color: var(--white-color);
}

.ingredient__name, .cocktail__name {
  font-family: var(--gallery-font-family);
  color: var(--modal-title-color);
  margin-top: 40px;
  margin-left: 32px;
  font-size: 32px;
  font-weight: 900;
  line-height: 1.2;
}

.ingredient__title, .cocktail__title {
  font-family: var(--primary-font-family);
  text-transform: uppercase;
  color: var(--modal-title-color);
  margin-top: 28px;
  margin-left: 32px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.5;
}

.cocktail__text {
  font-family: var(--primary-font-family);
  color: var(--modal-title-color);
  margin-top: 8px;
  margin-left: 32px;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.5;
}

.ingredient {
  height: 128px;
  min-width: 472px;
  overflow: overlay;
  margin-top: 8px;
  margin-left: 32px;
}

.ingredient__item {
  font-family: var(--primary-font-family);
  color: var(--modal-text-color);
  font-size: 16px;
  font-weight: 400;
  line-height: 1.6;
}

.ingredient__accent {
  color: var(--modal-title-color);
}

.ingredient-link {
  cursor: pointer;
}

.recipe__wrap {
  margin-left: 28px;
}

.recipe__title {
  font-family: var(--primary-font-family);
  text-transform: uppercase;
  color: var(--modal-title-color);
  margin-top: 28px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.6;
}

.recipe__text {
  max-width: 764px;
  font-family: var(--primary-font-family);
  color: var(--modal-text-color);
  max-height: 90px;
  overflow: overlay;
  margin-top: 10px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.75;
}

.btn--close {
  fill: var(--modal-close-icon-color);
  background-color: #0000;
  border: #0000;
  justify-content: flex-end;
  align-items: flex-start;
  margin-left: auto;
  padding: 0;
  display: flex;
  position: absolute;
  top: 12px;
  right: 12px;
}

.accent__text {
  color: var(--modal-accent-text-color);
  font-weight: 600;
}

.description__text {
  max-width: 654px;
  font-family: var(--primary-font-family);
  color: var(--modal-text-color);
  margin-top: 28px;
  margin-left: 32px;
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
}

.characteristic {
  margin-top: 28px;
  margin-bottom: 40px;
  margin-left: 28px;
}

.view {
  margin-bottom: 40px;
}

.line {
  width: 715px;
  border-bottom: 1px solid #fd5103;
}

.backdrop ::-webkit-scrollbar {
  width: 4px;
  border-radius: 4px;
}

.backdrop ::-webkit-scrollbar-track {
  background: #e0e0e0;
}

.backdrop ::-webkit-scrollbar-thumb {
  background: #fd5103;
  border-radius: 4px;
}

.backdrop-sign-in {
  width: 100%;
  height: 100%;
  background-color: #0003;
  transition: opacity .25s linear, visibility .25s linear;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop-sign-in .visually-hidden .modal {
  transform: translate(-50%, -50%)scale(0)rotate(-180deg);
}

.modal {
  transform: translate(-50%) scale(1) rotate(0);
  background-color: #fff;
  border-radius: 20%;
  padding: 40px;
  transition: all cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 93px;
  left: 50%;
}

@media screen and (min-width: 768px) {
  .modal {
    top: 208px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    padding: 50px 40px;
    top: 221px;
  }
}

.modal__close-btn {
  width: 30px;
  height: 30px;
  cursor: pointer;
  transition: var(--transition);
  background-color: #fff;
  border: 1px solid #0000001a;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal__close-btn:hover, .modal__close-btn:focus {
  fill: var(--white-color);
  background-color: var(--accent-color);
}

.modal__title {
  font-family: var(--title-font-family);
  text-align: center;
  letter-spacing: .03em;
  color: #212121;
  margin-bottom: 12px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.5;
}

@media screen and (min-width: 1200px) {
  .modal__title {
    font-size: 20px;
    line-height: 1.15;
  }
}

.modal-label {
  flex-direction: column;
  display: flex;
}

.modal-label:not(:last-of-type) {
  margin-bottom: 10px;
}

.modal-label:last-of-type {
  margin-bottom: 20px;
}

.modal-label__text {
  font-family: var(--font-main);
  letter-spacing: .01em;
  color: #757575;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.17;
}

.modal-label__input {
  transition: border var(--transition);
  width: 250px;
  height: 30px;
  border: 1px solid #21212133;
  border-radius: 4px;
  outline: none;
  padding: 5px 20px;
}

.modal-label__input:hover, .modal-label__input:focus {
  border: 1px solid var(--accent-color);
  cursor: pointer;
  outline: none;
}

.modal-policy {
  margin-bottom: 30px;
}

.modal-policy__label {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  display: flex;
}

.modal-policy__text {
  font-family: var(--font-main);
  letter-spacing: .03em;
  color: #757575;
  font-size: 12px;
  line-height: 1.17;
}

@media screen and (min-width: 1200px) {
  .modal-policy__text {
    font-size: 14px;
    line-height: 1.71;
  }
}

.modal-policy__link {
  color: var(--accent-color);
  border-bottom: 1px solid var(--accent-color);
  text-decoration: none;
}

.modal-policy__custom-check {
  width: 16px;
  height: 15px;
  border: 2px solid #212121;
  border-radius: 3px;
  justify-content: center;
  align-items: center;
  margin-right: 7px;
  display: flex;
}

.modal-policy__check {
  transition: border var(--transition);
}

.modal-policy__check:checked + .modal-policy__custom-check {
  border: 2px solid var(--accent-color);
  background-color: var(--accent-color);
}

.modal-policy__check:hover + .modal-policy__custom-check, .modal-policy__check:focus + .modal-policy__custom-check {
  border: 2px solid var(--accent-color);
}

.sign-in-btns {
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.modal-button-sign-in {
  font-family: var(--font-main);
  background-color: var(--accent-color);
  color: #fff;
  text-align: center;
  letter-spacing: .06em;
  cursor: pointer;
  transition: background-color var(--transition);
  border: 0;
  border-radius: 4px;
  align-items: center;
  margin: 0 auto;
  padding: 5px 10px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.88;
  display: block;
}

.modal-button-sign-in:hover, .modal-button-sign-in:focus {
  background-color: var(--accent-color);
  box-shadow: 0 3px 1px #0000001a, 0 1px 2px #00000014, 0 2px 2px #0000001f;
}

.header__container {
  align-items: flex-end;
  padding-top: 20px;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
}

.header__logo {
  font-family: var(--title-font-family);
  color: var(--accent-color);
  font-size: 22px;
  font-weight: 400;
  line-height: 1.1;
}

@media (min-width: 768px) {
  .header__logo {
    font-size: 28px;
    line-height: .86;
  }
}

.header__nav-list {
  display: none;
}

@media (min-width: 1280px) {
  .header__nav-list {
    gap: 60px;
    margin-left: 43px;
    display: flex;
  }
}

.header__nav-item {
  font-family: var(--title-font-family);
  letter-spacing: .02em;
  color: var(--manu-theme-color);
  font-size: 18px;
  font-weight: 400;
  line-height: 1.33;
}

.header__search {
  margin-left: 150px;
  position: relative;
}

@media screen and (max-width: 768px) {
  .header__search {
    display: none;
  }
}

.header__search-btn {
  width: 40px;
  height: 40px;
  background-color: #0000;
  border: 1px;
  position: absolute;
  left: 0;
}

.header__search-border:after {
  content: "";
  width: 39px;
  height: 1px;
  background-color: var(--accent-color);
  position: absolute;
  top: 19px;
  left: 20px;
  transform: rotate(90deg);
}

.header__search-icon {
  position: absolute;
  top: 50%;
  left: 8px;
  transform: translateY(-50%);
}

.header__search-input {
  width: 288px;
  height: 40px;
  background-color: var(--search-input-color);
  border: 1px solid #fd5103;
  border-radius: 5px;
  outline: none;
  padding: 8px 8px 8px 48px;
}

.header__theme-list {
  display: none;
}

@media (min-width: 1280px) {
  .header__theme-list {
    gap: 12px;
    margin-left: 150px;
    display: flex;
  }
}

.header__theme-item {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.33;
}

.header__theme-light {
  font-family: var(--title-font-family);
  color: var(--accent-theme-color);
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
}

.header__switcher {
  width: 40px;
  height: 20px;
  background: #fff;
  border: .604701px solid #fd5103;
  border-radius: 20px;
}

.header__theme-dark {
  font-family: var(--title-font-family);
  color: var(--theme-dark-color);
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
}

.btn__burger-menu-open {
  width: 40px;
  height: 40px;
  background-color: #0000;
  border: 0;
  margin-left: auto;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
}

@media (min-width: 1280px) {
  .btn__burger-menu-open {
    display: none;
  }
}

.menu-open__icon {
  fill: var(--btn-mob-menu-color);
  transition: fill var(--transition);
}

.menu-open__icon:hover, .menu-open__icon:focus {
  fill: var(--accent-text-color);
}

.toggle-button {
  width: 40px;
  height: 20px;
  vertical-align: top;
  cursor: pointer;
  -webkit-appearance: none;
  appearance: none;
  background: #fff;
  border: .604701px solid #fd5103;
  border-radius: 20px;
  outline: none;
  margin: 0;
  transition: all .3s cubic-bezier(.2, .85, .32, 1.2);
  position: relative;
}

.toggle-button:after {
  content: "";
  width: 16px;
  height: 16px;
  background-color: var(--accent-color);
  border-radius: 50%;
  transition: all .3s cubic-bezier(.2, .85, .32, 1.2);
  position: absolute;
  top: 1px;
  left: 1px;
  transform: translateX(0);
}

.toggle-button:checked:after {
  background-color: #fff;
  transform: translateX(calc(100% + 3px));
}

.toggle-button:checked {
  background-color: var(--accent-color);
}

.login-btn {
  font-family: var(--title-font-family);
  background-color: var(--accent-color);
  color: #fff;
  margin-left: auto;
  padding: 5px 15px;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.33;
}

.header__logo {
  position: relative;
}

.coctail-header {
  fill: #fd5103;
  z-index: -1;
  position: absolute;
  bottom: 11px;
  left: 78.8px;
}

@media (min-width: 768px) {
  .coctail-header {
    bottom: 11px;
    left: 100.5px;
  }
}

.drink-header {
  animation: myframes 2s ease-in-out;
  position: absolute;
  bottom: 21px;
  left: 85.8px;
}

@media (min-width: 768px) {
  .drink-header {
    bottom: 23px;
    left: 109.5px;
  }
}

@keyframes myframes {
  0% {
    transform: scale(1)translateY(0);
  }

  10% {
    transform: scale(0, .2)translateY(8px);
  }
}

.drink-white {
  z-index: -1;
  position: absolute;
  bottom: 21px;
  left: 85.8px;
}

@media (min-width: 768px) {
  .drink-white {
    bottom: 23px;
    left: 109.5px;
  }
}

.hero-container {
  flex-direction: column;
  align-items: center;
  display: flex;
}

@media (min-width: 768px) {
  .hero-container {
    flex-direction: row;
    justify-content: space-between;
  }
}

.hero__title {
  font-family: var(--primary-font-family);
  color: var(--accent-color);
  max-width: 280px;
  font-size: 22px;
  font-weight: 600;
  line-height: 42px;
}

@media (min-width: 768px) {
  .hero__title {
    max-width: 336px;
  }
}

@media (min-width: 1280px) {
  .hero__title {
    max-width: 688px;
    font-size: 52px;
    line-height: 1.54;
  }
}

.hero__list {
  display: none;
